.footer{
  padding: 2.75em 0 4.5em;
  background: #1C1A28;
  color: #FFF;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col2,
  &__col3{
    padding-top: 4em;
  }
  &__logo{
    display: block;
    width: 7em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__protect{
    margin-top: 1.44em;
  }
  &__links,
  &__links2{
    margin-top: 1.125em;
    a{
      color: rgba(#fff,.6);
      font-size: 1.125em;
      line-height: 139%;
      &:hover{
        color: #fff;
      }
    }
  }
  &__links{
    ul{
      @include grid100gap(1em);
    }
  }
  &__links2{
    ul{
      column-count: 2;
      column-gap: 3.75em;
    }
    li{
      margin-bottom: 1.125em;
    }
    a{
      white-space: nowrap;
    }
  }
  .socials{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(4,1.875em);
    gap: .625em 1em;
    &__item{
      margin: 0 !important;
    }
  }
  &__c{
    margin-top: 2.5em;
    max-width: 17.5em;
    opacity: .89;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3.44em 0 5em;
    &__inner{
      @include grid100gap(3.69em);
    }
    &__col2{
      padding-top: .31em;
    }
    &__col3{
      padding: 0;
    }
  }
}


