.faq{
  padding: 5em 0 3.625em;
  &__items{
    @include grid100gap(1.5625em);
    margin-top: 2.5em;
  }
}

.faq-card{
  &._active{
    .qa-card__btn{
      span{
        transform: rotate(-45deg);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      cursor: pointer;
    }
  }
  &__btn{
    width: 1em;
    height: 1em;
    @include center;
    font-weight: 700;
    span{
      font-size: 1.25em;
      line-height: 125%;
      transition: all .3s;
    }
    &:hover{
      color: $primary;
    }
  }
  &__hidden{
    display: none;
    padding-top: 1.5625em;
  }
}

.news{
  padding: 3.625em 0 2.4em;
  overflow: hidden;
}

.news-slider{
  margin-top: 1.81em;
}

.news-card{
  &__img{
    clip-path: polygon( 0% 14.116%,0% 14.116%,0.062% 13.358%,0.244% 12.633%,0.536% 11.951%,0.929% 11.32%,1.416% 10.748%,1.987% 10.245%,2.634% 9.82%,3.348% 9.481%,4.12% 9.237%,4.942% 9.097%,93.831% 0.118%,93.831% 0.118%,94.803% 0.097%,95.736% 0.224%,96.615% 0.487%,97.424% 0.874%,98.15% 1.374%,98.776% 1.975%,99.289% 2.663%,99.674% 3.428%,99.916% 4.257%,100% 5.138%,100% 85.884%,100% 85.884%,99.938% 86.642%,99.756% 87.367%,99.464% 88.049%,99.071% 88.68%,98.584% 89.252%,98.013% 89.755%,97.366% 90.18%,96.652% 90.519%,95.88% 90.763%,95.058% 90.903%,6.169% 99.882%,6.169% 99.882%,5.197% 99.903%,4.264% 99.776%,3.385% 99.513%,2.576% 99.126%,1.85% 98.626%,1.224% 98.025%,0.711% 97.337%,0.326% 96.572%,0.084% 95.743%,0% 94.862%,0% 14.116% );
    @include imgCover(111%);
    margin-bottom: 1.25em;
  }
  &__text{
    @include grid100gap(.25em);
  }
  &__title{
    font-size: 1.5625em;
    line-height: 144%;
    font-weight: 600;
  }
  &__desc{
    line-height: 138%;
  }
}

.contacts-index{
  padding: 2.4em 0 4.75em;
  &__inner{
    clip-path: polygon( 0% 22.18%,0% 22.18%,0.049% 20.271%,0.191% 18.456%,0.419% 16.759%,0.725% 15.201%,1.102% 13.808%,1.542% 12.603%,2.038% 11.609%,2.583% 10.85%,3.169% 10.35%,3.788% 10.131%,95.931% 0.033%,95.931% 0.033%,96.585% 0.126%,97.208% 0.537%,97.79% 1.237%,98.324% 2.202%,98.799% 3.405%,99.208% 4.82%,99.541% 6.422%,99.79% 8.183%,99.946% 10.078%,100% 12.081%,100% 77.688%,100% 77.688%,99.951% 79.596%,99.809% 81.411%,99.581% 83.109%,99.275% 84.666%,98.898% 86.059%,98.458% 87.265%,97.962% 88.259%,97.417% 89.017%,96.832% 89.518%,96.212% 89.736%,4.069% 99.835%,4.069% 99.835%,3.415% 99.741%,2.792% 99.331%,2.209% 98.631%,1.676% 97.666%,1.201% 96.463%,0.792% 95.047%,0.459% 93.446%,0.21% 91.685%,0.054% 89.79%,0% 87.787%,0% 22.18% );
    padding: 5.6em 6em 5.44em 2.5em;
    background: linear-gradient(0deg, #F0F0F0 0%, #F0F0F0 100%), rgba(255, 255, 255, 0.15);
  }
  &__wrap{
    margin-top: 2em;
  }
  &__items{
    @include grid100gap(.81em);
  }
  &__item{
    font-size: 1.25em;
    line-height: 145%;
    font-weight: 600;
  }
}

.catalog{
  padding: 3.75em 0 2.75em;
  &__grid{
    align-items: flex-start;
    display: grid;
    grid-template-columns: 34.375em calc(100% - 34.375em - 5.875em);
    justify-content: space-between;
    gap: 3em;
  }
  &__col2{
    position: relative;
  }
  &__desc2,
  &__desc{
    font-size: 1.25em;
    line-height: 150%;
    margin-top: 1.15em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
    gap: .625em;
  }
  &__item{
    @include imgCover(100%);
    border-radius: 1.25em;
  }
  &__logo{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include center;
    max-width: 80%;
    img{
      transform: scale(.7) rotate(45deg);
      opacity: 0;
      width: 25em;
      max-width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding: 3.1em 0 2.8em;
  }

  .news{
    padding: 2.8em 0 2.2em;
  }

  .news-slider{
    margin-top: 1em;
  }

  .contacts-index{
    padding: 2.2em 0 6.6em;
    &__inner{
      padding: 3.7em 1.37em 4.25em;
    }
  }

  .catalog{
    padding: 2.75em 0 3em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.5em));
    }
  }
}



@media screen and (max-width: 576px){
  .contacts-index{
    &__inner{
      clip-path: polygon( 100% 5.468%,100% 5.468%,99.961% 5.067%,99.848% 4.686%,99.667% 4.33%,99.423% 4.002%,99.123% 3.71%,98.773% 3.456%,98.377% 3.247%,97.944% 3.087%,97.477% 2.982%,96.983% 2.935%,3.251% 0.098%,3.251% 0.098%,2.729% 0.117%,2.231% 0.203%,1.766% 0.35%,1.34% 0.553%,0.96% 0.806%,0.633% 1.103%,0.367% 1.44%,0.168% 1.811%,0.043% 2.21%,0% 2.631%,0% 94.532%,0% 94.532%,0.039% 94.933%,0.152% 95.314%,0.333% 95.671%,0.577% 95.998%,0.877% 96.29%,1.227% 96.544%,1.622% 96.753%,2.056% 96.913%,2.523% 97.018%,3.017% 97.065%,96.749% 99.902%,96.749% 99.902%,97.271% 99.883%,97.769% 99.797%,98.234% 99.65%,98.66% 99.447%,99.04% 99.194%,99.367% 98.897%,99.633% 98.56%,99.832% 98.189%,99.957% 97.79%,100% 97.369%,100% 5.468% );
    }
    &__wrap{
      display: block;
    }
    &__btn{
      margin-top: 2.5em;
    }
  }

  .catalog{
    text-align: center;
    &__grid{
      grid-template-columns: 100%;
    }
    &__desc{
      display: none;
    }
    &__desc2{
      display: block;
      margin: 0;
    }
  }
}
