.reviews{
  padding: 5.5em 0 5em;
  overflow: hidden;
  &__top{
    padding-right: 2.25em;
    margin-bottom: 2em;
  }
}

.m-tab{
  height: 2.125em;
  padding: .25em 0;
  font-weight: 600;
  opacity: .5;
  margin-right: 4em;
  transition: all .3s;
  cursor: pointer;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 1.25em;
    height: 2px;
    background-color: $primary;
    opacity: 0;
  }
  &:hover{
    opacity: 1;
  }
  &:last-child{
    margin-right: 0;
  }
  &._active{
    opacity: 1;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
  span{
    font-size: 1.25em;
  }
}

.news-slider,
.reviews-slider{
  .swiper{
    &-container{
      width: calc(100% + 2.1875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      height: auto;
      padding-right: 2.1875em;
    }
  }
}

