  height: 100%;
  display: flex;
  flex-direction: column;
  &__img{
    clip-path: polygon( 100% 14.116%,100% 14.116%,99.938% 13.358%,99.756% 12.633%,99.464% 11.951%,99.071% 11.32%,98.584% 10.748%,98.013% 10.245%,97.366% 9.82%,96.652% 9.481%,95.88% 9.237%,95.058% 9.097%,6.169% 0.118%,6.169% 0.118%,5.197% 0.097%,4.264% 0.224%,3.385% 0.487%,2.576% 0.874%,1.85% 1.374%,1.224% 1.975%,0.711% 2.663%,0.326% 3.428%,0.084% 4.257%,0% 5.138%,0% 85.884%,0% 85.884%,0.062% 86.642%,0.244% 87.367%,0.536% 88.049%,0.929% 88.68%,1.416% 89.252%,1.987% 89.755%,2.634% 90.18%,3.348% 90.519%,4.12% 90.763%,4.942% 90.903%,93.831% 99.882%,93.831% 99.882%,94.803% 99.903%,95.736% 99.776%,96.615% 99.513%,97.424% 99.126%,98.15% 98.626%,98.776% 98.025%,99.289% 97.337%,99.674% 96.572%,99.916% 95.743%,100% 94.862%,100% 14.116% );
    @include imgCover(111%);
    margin-bottom: .44em;
  }
  &__play{
    @include centerAbsolute;
    background-image: url(../img/play.svg);
    width: 2.375em;
    height: 2.75em;
    @include bgImgSize(100%)
  }
  &__text{
    flex-grow: 1;
    clip-path: polygon( 100% 14.116%,100% 14.116%,99.938% 13.358%,99.756% 12.633%,99.464% 11.951%,99.071% 11.32%,98.584% 10.748%,98.013% 10.245%,97.366% 9.82%,96.652% 9.481%,95.88% 9.237%,95.058% 9.097%,6.169% 0.118%,6.169% 0.118%,5.197% 0.097%,4.264% 0.224%,3.385% 0.487%,2.576% 0.874%,1.85% 1.374%,1.224% 1.975%,0.711% 2.663%,0.326% 3.428%,0.084% 4.257%,0% 5.138%,0% 85.884%,0% 85.884%,0.062% 86.642%,0.244% 87.367%,0.536% 88.049%,0.929% 88.68%,1.416% 89.252%,1.987% 89.755%,2.634% 90.18%,3.348% 90.519%,4.12% 90.763%,4.942% 90.903%,93.831% 99.882%,93.831% 99.882%,94.803% 99.903%,95.736% 99.776%,96.615% 99.513%,97.424% 99.126%,98.15% 98.626%,98.776% 98.025%,99.289% 97.337%,99.674% 96.572%,99.916% 95.743%,100% 94.862%,100% 14.116% );
    background-color: #D9D9D9;
    padding: 2.62em 1.31em 1.56em;
    min-height: 25em;
  }
  &__title{
    font-size: 1.25em;
    line-height: 140%;
    font-weight: 600;
  }
  &__desc{
    margin-top: 1.37em;
    font-size: 1em;
    line-height: 140%;
    *{
      line-height: inherit;
    }
    p{
      margin-bottom: 1.25em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  &__stars{
    justify-content: flex-end;
    margin-top: 2em;
    font-weight: 600;
    img{
      width: 1.25em;
      margin-left: .19em;
    }
  }
}


@media screen and (max-width: 992px){
  .reviews{
    padding: 2.2em 0 3.1em;
    &__top{
      padding-right: 0;
      margin-bottom: 2.625em;
    }
  }

  .news-slider,
  .reviews-slider{
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 19.9375em;
        padding-right: 1.0625em;
      }
    }
  }

  .review-card{
    font-size: .875em;
    &__text{
      margin-top: -1em;
      padding: 1.5em 1.25em 1em;
      &:first-child{
        margin-top: 0;
      }
    }
  }

  .m-tab{
    margin-right: 3.375em;
  }
}


@media screen and (max-width: 576px){
  .reviews{
    &__top{
      display: block;
    }
    .m-tabs{
      margin-top: 1.5em;
    }
  }
}
