.advantages{
  padding-bottom: 1.6em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .625em;
    margin-top: 2.375em;
    &:first-child{
      margin-top: 0;
    }
  }
}

.advantage-item{
  &__icon{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 7.5em;
    img{
      max-width: 6.25em;
      max-height: 100%;
    }
  }
  &__text{
    margin-top: .9375em;
    text-align: center;
    @include grid100gap(.9375em);
  }
  &__title{
    font-size: 1.38em;
    line-height: 140%;
    font-weight: 600;
  }
  &__desc{
    font-size: 1.13em;
    line-height: 140%;
  }
  &__num{
    clip-path: polygon( 0% 33.678%,0% 33.678%,0.048% 33.048%,0.19% 32.436%,0.422% 31.847%,0.737% 31.288%,1.132% 30.762%,1.603% 30.277%,2.144% 29.836%,2.75% 29.446%,3.419% 29.112%,4.143% 28.84%,90.849% 1.138%,90.849% 1.138%,92.167% 0.834%,93.479% 0.749%,94.753% 0.866%,95.959% 1.17%,97.064% 1.643%,98.038% 2.269%,98.85% 3.032%,99.468% 3.915%,99.862% 4.902%,100% 5.976%,100% 66.322%,100% 66.322%,99.952% 66.952%,99.81% 67.564%,99.578% 68.153%,99.263% 68.712%,98.868% 69.238%,98.397% 69.723%,97.856% 70.164%,97.25% 70.554%,96.581% 70.888%,95.857% 71.16%,9.151% 98.862%,9.151% 98.862%,7.833% 99.166%,6.521% 99.251%,5.247% 99.134%,4.041% 98.83%,2.936% 98.357%,1.962% 97.731%,1.15% 96.968%,0.532% 96.085%,0.138% 95.098%,0% 94.024%,0% 33.678% );
    width: 5.5em;
    height: 7.25em;
    margin: 0 auto;
    filter: drop-shadow(4px 11px 33px rgba(1, 232, 165, 0.44));
    background-color: $primary;
    @include center;
    font-weight: 600;
    span{
      font-size: 1.5625em;
    }
  }
}

.how-get{
  padding-bottom: 1.5em;
  .h1{
    text-align: center;
  }
}


@media screen and (max-width: 992px){
  .advantages{
    padding-bottom: 4em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .advantage-item{
    &__icon{
      height: auto;
    }
  }
}


