.main{
  padding: 10em 0 5.25em;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  &__content{
    max-width: 43.5em;
  }
  .text{
    margin-top: 1.1em;
  }
  &__nav{
    display: flex;
    margin-top: 2.625em;
  }
  &__btn{
    padding: 0 5em;
  }
  &__img{
    position: absolute;
    top: -5.25em;
    right: -12.5em;
    width: 48.125em;
  }
}

.what{
  padding: 9em 0 3.75em;
  margin-top: -3.75em;
  &__inner{
    position: relative;
  }
  &__content{
    width: 30em;
    max-width: 100%;
    margin-top: 2.125em;
    position: relative;
  }
  &__items{
    @include grid100gap(2.375em);
  }
  &__lines{
    position: absolute;
    width: 22em;
    top: 3em;
    left: 3.125em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__img{
    position: absolute;
    right: -10em;
    top: -9em;
    width: 48.125em;
  }
}

.what-item{
  padding: 1.125em 1.25em;
  border-radius: 0.3125em;
  background: #F0F0F0;
  width: 22.1875em;
  max-width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  &:first-child{
    padding-top: .625em;
    padding-bottom: .625em;
  }
  &:nth-child(even){
    margin-left: auto;
    width: 24.625em;
    .what-item__icon{
      left: unset;
      right: 100%;
      margin: 0 2.1875em 0 0;
    }
  }
  .text17{
    line-height: 141%;
  }
  &__icon{
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translateY(-40%);
    margin-left: 3em;
    max-width: 3.75em;
    max-height: 3.75em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 7.875em 0 3.5em;
    .h1{
      font-size: 1.75em;
      font-weight: 400;
    }
    &__img{
      position: static;
      width: 30em;
      margin: .5em auto 0;
    }
    .text{
      margin-top: 1.67em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .what{
    padding: 3.75em 0;
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    .h1{
      text-align: center;
      span{
        &:first-child{
          display: block;
        }
      }
    }
    &__img{
      width: calc(100% + 3.75em);
      margin-left: -1.875em;
    }
    &__nav{
      justify-content: center;
    }
  }

  .what{
    &__content{
      font-size: .729em;
    }
  }
}
