.mistake {
  padding: 10.9375em 0 8.75em;
  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7.5em;
  }
  &__img {
    position: relative;
    width: 29.375em;
    flex-shrink: 0;
    img:nth-child(1) {
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% + 6.25em);
    }
  }
  &__title {
    span {
      font-size: 1.5625em;
      line-height: 120%;
      font-weight: 600;
    }
  }
  &__btn {
    height: 3.75em;
    padding: 0 1.25em;
    margin-top: 1.25em;
    span {
      font-size: 1.0625em;
    }
  }
}

@media screen and (max-width: 992px) {
  .mistake {
    padding: 10em 0 6.25em;
    &__inner {
      gap: 4.0625em;
    }
    &__img {
      width: 24.375em;
      padding: 0 1.875em;
      img:nth-child(2) {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    &__inner {
      flex-direction: column;
    }
    &__img {
      max-width: 24.375em;
      width: 100%;
    }
    &__desc {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__btn {
      margin-top: 3.4375em;
    }
  }
}