@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 3.75em;
    height: 5.375em;
    border-radius: 20em;
    color: #111;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.5em;
        margin-top: -.25em;
    }
}

.m-btn-green{
    box-shadow: 0 2em 3.5em 0 rgba(0, 204, 135, 0.33);
    background-color: $primary;
    color: #111;
    &:hover{
        background-color: darken($primary,10%);
        color: #111;
    }
}
