.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  select,
  input,
  textarea{
    border-radius: 20em;
    border: none;
    padding: 0 1em;
    height: 3.6em;
    width: 100%;
    font-size: 1.125em;
    background-color: #fff;
    color: $black;
    font-weight: 600;
    &::placeholder {
      color: $black;
    }
    &:focus {
      border-color: #9CA0A0;
      box-shadow: 0 .25em .4375em 0 rgba(47, 63, 67, 0.03), 0 .25em .3125em 0 rgba(0, 0, 0, 0.02) inset;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/select-arrow.svg);
    background-size: .9em auto;
    background-repeat: no-repeat;
    background-position: right 1em center;
    &::-ms-expand {
      display: none; // IE
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  &._error{
    select,
    input,
    textarea{
      background: #FFCDCD;
      color: #FF6767;
      &::placeholder{
        color: #FF6767;
      }
    }
  }
  &._success{
    select,
    input,
    textarea{
      background: #B5FFEA;
      color: #00A676;
      &::placeholder{
        color: #00A676;
      }
    }
  }
}


.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.8125em;
    min-height: 1.25em;
    font-weight: 600;
    span{
      font-size: .85em;
      line-height: 140%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      background-color: #D9D9D9;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: 1em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check.svg);
    }
  }
}



