.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 1.19em;
  z-index: 10;
  .container{
    max-width: 1440px;
  }
  &__inner{
    display: flex;
    align-items: center;
    padding: .5em 1.25em;
    border: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 0.625em;
    background: rgba(255, 255, 255, 0.75);
    backdrop-filter: blur(50px);
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 10em;
    margin-right: 7.5em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__btn{
    padding: 0 1.25em;
    height: 3.75em;
    width: auto;
    margin-left: auto;
    span{
      font-size: 1.0625em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 1.25em;
    line-height: 145%;
    color: rgba($black,.6);
    margin-right: 2.75em;
    font-weight: 500;
    &:last-child{
      margin: 0;
    }
    &--hide{
      display: none;
    }
  }
}

.menu__bottom,
.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 0;
    position: absolute;
    .container{
      padding: 0 1.125em;
    }
    &__inner{
      padding: 0;
      backdrop-filter: unset;
      border: none;
      background-color: transparent;
    }
    &__logo{
      margin: 0;
      width: 7em;
      z-index: 11;
      position: relative;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__btn{
      display: none;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    padding: 11em 2.375em 2em;
    z-index: 10;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    &__bottom{
      display: block;
      margin-top: auto;
      padding-top: 2em;
    }
    &__c{
      margin-top: 2.625em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      margin: 0 0 1em;
      font-size: 1.125em;
      color: $black;
      text-transform: capitalize;
      &--hide{
        display: unset;
      }
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 3.125em;
    height: 5.375em;
    display: flex;
    background-color: $primary;
    filter: drop-shadow(4px 11px 33px rgba(1, 232, 165, 0.44));
    margin-left: auto;
    clip-path: polygon( 0% 33.809%,0% 33.809%,0.071% 33.005%,0.279% 32.22%,0.62% 31.46%,1.086% 30.732%,1.674% 30.04%,2.376% 29.392%,3.187% 28.793%,4.101% 28.249%,5.112% 27.766%,6.216% 27.35%,82.818% 1.948%,82.818% 1.948%,85.192% 1.352%,87.597% 1.098%,89.965% 1.162%,92.232% 1.517%,94.331% 2.141%,96.196% 3.008%,97.761% 4.092%,98.961% 5.37%,99.729% 6.817%,100% 8.407%,100% 66.191%,100% 66.191%,99.929% 66.995%,99.721% 67.78%,99.38% 68.54%,98.914% 69.268%,98.326% 69.96%,97.624% 70.608%,96.813% 71.207%,95.899% 71.751%,94.888% 72.234%,93.784% 72.65%,17.182% 98.052%,17.182% 98.052%,14.808% 98.648%,12.403% 98.902%,10.035% 98.838%,7.768% 98.483%,5.669% 97.859%,3.804% 96.993%,2.239% 95.908%,1.039% 94.63%,0.271% 93.183%,0% 91.593%,0% 33.809% );
    img{
      @include centerAbsolute;
      width: 1.1875em;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
    }
  }
}


