.contacts {
  padding: 10.9375em 0 8.75em;
  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    gap: 1.25em;
    margin-top: 1.25em;
  }
  &__group {
    position: relative;
    padding: 3.125em 2em 4em;
    &:nth-child(1) {
      &:after {
        background: rgba(1, 232, 165, 0.5);
      }
    }
    &:nth-child(2) {
      &:after {
        background: #D9D9D9;
      }
      .contacts__items {
        grid-template-columns: 100%;
      }
    }
    &:after {
      content: '';
      position: absolute;
      top: 1em;
      left: 50%;
      transform: translate(-50%, 0);
      width: 100%;
      height: 100%;
      clip-path: polygon( 100% 20.491%,100% 20.491%,99.961% 19.715%,99.846% 18.973%,99.662% 18.275%,99.413% 17.629%,99.106% 17.044%,98.745% 16.53%,98.337% 16.095%,97.886% 15.748%,97.398% 15.498%,96.879% 15.354%,3.897% 0.122%,3.897% 0.122%,3.282% 0.099%,2.693% 0.229%,2.138% 0.499%,1.627% 0.895%,1.169% 1.407%,0.773% 2.021%,0.449% 2.726%,0.206% 3.508%,0.053% 4.356%,0% 5.258%,0% 79.509%,0% 79.509%,0.039% 80.285%,0.154% 81.027%,0.338% 81.725%,0.587% 82.371%,0.894% 82.956%,1.255% 83.47%,1.663% 83.905%,2.114% 84.252%,2.602% 84.502%,3.121% 84.646%,96.103% 99.879%,96.103% 99.879%,96.718% 99.901%,97.307% 99.771%,97.862% 99.501%,98.373% 99.105%,98.831% 98.593%,99.227% 97.979%,99.551% 97.274%,99.794% 96.492%,99.947% 95.644%,100% 94.742%,100% 20.491% );
      pointer-events: none;
    }
  }
  &__subtitle {
    position: relative;
    z-index: 2;
    span {
      font-size: 1.25em;
      line-height: 140%;
      font-weight: 600;
    }
  }
  &__items {
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    gap: 1.25em;
    margin-top: 1.25em;
  }
  &__item {
    &-text {
      span {
        font-size: 1em;
        line-height: 130%
      }
    }
    &-link {
      span {
        font-size: 1.5625em;
        line-height: 140%;
        text-decoration: underline;
        text-underline-offset: 0.125em;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding: 10em 0 2.8125em;
    &__wrap {
      margin-top: 1.875em;
    }
    &__group {
      padding: 3.75em 1.25em 5.625em;
      &:after {
        top: 0;
      }
    }
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      &-link {
        span {
          font-size: 1.25em;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__wrap {
      grid-template-columns: 100%;
      gap: 0;
    }
    &__group {
      &:nth-child(2) {
        margin-top: -1.875em;
      }
    }
    &__item {
      &-link {
        span {
          font-size: 1.5625em;
        }
      }
    }
  }
}