.politics {
  padding: 10.9375em 0 8.75em;
  &__text {
    margin-top: 2.5em;
    p {
      font-size: 1.5625em;
      line-height: 140%;
      &:not(:last-child) {
        margin-bottom: 2.5em;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .politics {
    padding: 10em 0 6.25em;
    &__text {
      margin-top: 1.5em;
      p {
        font-size: 0.9375em;
        &:not(:last-child) {
          margin-bottom: 1.5em;
        }
      }
    }
  }
}