      max-width: 23.125em;
      text-align: right;
      margin-bottom: 1em;
      p {
        font-size: 1.25em;
        line-height: 120%;
      }
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    max-width: 37.5em;
  }
  &__text {
    margin-top: 5.625em;
    p {
      font-size: 1.5625em;
      line-height: 140%;
      &:not(:last-child) {
        margin-bottom: 2.5em;
      }
    }
