@media screen and (max-width: 992px) {
  .article {
    padding: 10em 0 4.3125em;
    &-banner {
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      text-align: center;
      padding: 4.6875em 1em;
      &__text {
        text-align: center;
        margin-top: 0;
        p {
          font-size: 1.0625em;
        }
      }
    }
    &__text {
      margin-top: 3.125em;
      p {
        font-size: 0.9375em;
        &:not(:last-child) {
          margin-bottom: 1.5em;
        }
      }
      img {
        margin-bottom: 1.5em;
      }
    }
  }
}

