.tariffs {
  padding: 9em 0 4.5em;
  &__title {
    text-align: center;
  }
  .m-tabs {
    justify-content: center;
    margin-top: 1.875em;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.125em / 2));
    align-items: center;
    gap: 3.125em;
    margin-top: 3.125em;
  }
  ._tab1 {
  }
  &__left {
    position: relative;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  .tariff-card {
    position: absolute;
    left: 0;
    bottom: 2em;
    width: 100%;
    color: #fff;
    padding: 0 2.8125em;
    &__number {
      span {
        font-size: 2.5em;
        line-height: 120%;
      }
    }
    &__name {
      margin-top: 0.75em;
      span {
        font-size: 1.5em;
        line-height: 120%;
        font-weight: 600;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1em / 2));
    gap: 2.5em 1em;
  }
  &__item {
    &-title {
      span {
        font-size: 1.125em;
        line-height: 140%;
      }
    }
    &-text {
      margin-top: 0.25em;
      span {
        font-size: 1.6875em;
        line-height: 140%;
        font-weight: 600;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .tariffs {
    padding: 10em 0 4em;
    .m-tabs {
      margin-top: 1.5em;
    }
    &__content {
      grid-template-columns: 100%;
      gap: 2.5em;
      margin-top: 2.5em;
    }
    .tariff-card {
      &__number {
        span {
          font-size: 1.5625em;
        }
      }
      &__name {
        span {
          font-size: 1em;
        }
      }
    }
    &__items {
      gap: 1.25em 1em;
    }
    &__item {
      &-title {
        span {
          font-size: 0.9375em;
        }
      }
      &-text {
        span {
          font-size: 1.3125em;
        }
      }
    }
  }
}