.compare{
  padding: 2.75em 0;
  &__title{
    span{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        @include centerAbsolute;
        width: calc(100% + .25em);
        height: 2.125em;
        clip-path: polygon( 0% 15.352%,0% 15.352%,0.021% 14.599%,0.081% 13.883%,0.178% 13.213%,0.308% 12.599%,0.468% 12.049%,0.655% 11.573%,0.865% 11.18%,1.097% 10.88%,1.346% 10.681%,1.609% 10.593%,98.265% 0.187%,98.265% 0.187%,98.543% 0.222%,98.809% 0.383%,99.057% 0.659%,99.285% 1.04%,99.488% 1.515%,99.662% 2.074%,99.804% 2.707%,99.911% 3.404%,99.977% 4.153%,100% 4.945%,100% 84.648%,100% 84.648%,99.979% 85.401%,99.919% 86.117%,99.822% 86.787%,99.692% 87.401%,99.532% 87.951%,99.345% 88.427%,99.135% 88.82%,98.903% 89.12%,98.654% 89.319%,98.391% 89.407%,1.735% 99.813%,1.735% 99.813%,1.457% 99.778%,1.191% 99.617%,0.943% 99.341%,0.715% 98.96%,0.512% 98.485%,0.338% 97.926%,0.196% 97.293%,0.09% 96.596%,0.023% 95.847%,0% 95.055%,0% 15.352% );
        background-color: $primary;
        z-index: -1;
        top: 52%;
      }
    }
  }
}

.compare-table{
  margin-top: 2.5em;
  table{
    width: 100%;
  }
  td{
    padding: .75em .25em .75em 0;
    text-align: center;
    &:first-child{
      text-align: left;
    }
    span{
      font-size: 1.25em;
      line-height: 140%;
    }
    img{
      width: auto;
      height: .625em;
      display: block;
      margin: 0 auto;
    }
  }
}

.condition{
  padding: 2.75em 0 5.5em;
  &__props{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .75em));
    gap: .25em 1.125em;
    margin-top: 1.5em;
  }
}

.condition-prop{
  background-color: #F0F0F0;
  padding: 1.69em 2.19em 2.13em;
  clip-path: polygon( 100% 30.568%,100% 30.568%,99.91% 27.218%,99.647% 24.03%,99.226% 21.048%,98.661% 18.313%,97.965% 15.865%,97.152% 13.746%,96.235% 11.997%,95.228% 10.661%,94.145% 9.777%,92.999% 9.388%,7.546% 0.044%,7.546% 0.044%,6.333% 0.203%,5.179% 0.92%,4.099% 2.149%,3.11% 3.844%,2.228% 5.959%,1.469% 8.447%,0.851% 11.264%,0.389% 14.364%,0.1% 17.699%,0% 21.224%,0% 69.432%,0% 69.432%,0.091% 72.782%,0.353% 75.97%,0.774% 78.952%,1.339% 81.687%,2.035% 84.135%,2.848% 86.254%,3.765% 88.002%,4.772% 89.339%,5.855% 90.223%,7.001% 90.612%,92.454% 99.956%,92.454% 99.956%,93.667% 99.797%,94.821% 99.08%,95.901% 97.851%,96.89% 96.156%,97.772% 94.042%,98.531% 91.553%,99.149% 88.736%,99.611% 85.636%,99.9% 82.301%,100% 78.776%,100% 30.568% );
  &__title{
    font-size: 1.13325em;
    line-height: 137%;
  }
  &__desc{
    font-size: 1.6875em;
    line-height: 120%;
    font-weight: 600;
    margin-top: .125em;
  }
  &:nth-child(2){
    transform: translateY(1em);
  }
  &:nth-child(3){
    transform: translateY(2em);
  }
}

.condition-form{
  position: relative;
  padding: 3.875em 3em 3.625em 3.5em;
  background-color: #F0F0F0;
  clip-path: polygon( 100% 18.653%,100% 18.653%,99.957% 17.161%,99.832% 15.741%,99.633% 14.413%,99.365% 13.195%,99.034% 12.105%,98.648% 11.163%,98.213% 10.385%,97.736% 9.791%,97.222% 9.4%,96.679% 9.229%,3.568% 0.113%,3.568% 0.113%,2.994% 0.186%,2.448% 0.507%,1.937% 1.055%,1.47% 1.81%,1.053% 2.751%,0.694% 3.858%,0.402% 5.11%,0.184% 6.488%,0.047% 7.971%,0% 9.537%,0% 81.347%,0% 81.347%,0.043% 82.839%,0.168% 84.259%,0.367% 85.587%,0.635% 86.805%,0.966% 87.895%,1.352% 88.837%,1.787% 89.615%,2.264% 90.209%,2.778% 90.6%,3.322% 90.771%,96.432% 99.887%,96.432% 99.887%,97.006% 99.814%,97.552% 99.493%,98.062% 98.945%,98.53% 98.19%,98.947% 97.249%,99.306% 96.142%,99.598% 94.89%,99.816% 93.512%,99.953% 92.029%,100% 90.463%,100% 18.653% );
  margin-top: .5em;
  display: grid;
  grid-template-columns: 23.125em calc(100% - 23.125em);
  @media screen and (min-width: 993px){
    .h1{
      font-size: 2.76em;
    }
  }
  &__col{
    position: relative;
  }
  &__arrow{
    position: absolute;
    top: 11em;
    left: 8em;
    width: 13.75em;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.25em 1.125em;
  }
  .m-check{
    margin-top: 1.25em;
    max-width: 35em;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: 1.5em;
  }
  &__btn{
    height: 4.5em;
    width: 21em;
    max-width: 100%;
    span{
      font-size: 1.125em;
    }
  }
  &__success{
    position: absolute;
    top: 7.5em;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    text-align: center;
    opacity: 0;
    pointer-events: none;
    &-icon{
      width: 5.75em;
      margin-bottom: 1.125em;
    }
    &-title{
      font-size: 2.75em;
      line-height: 120%;
      font-weight: 600;
    }
  }
  &__desc{
    font-size: 1.375em;
    line-height: 120%;
    font-weight: 600;
    margin-top: 1.3em;
  }
  &._success{
    background-color: #01E8A5;
    .condition-form__col,
    .condition-form__col2{
      opacity: 0;
      pointer-events: none;
    }
    .condition-form__success{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .compare{
    padding: 3.9em 0;
  }

  .compare-table {
    overflow-x: auto;
    width: calc(100% + 4.75em);
    padding: 0 2.375em 2em;
    margin-left: -2.375em;
    @include scroll;
    table {
      min-width: 70em;
      font-size: .7em;
    }
  }

  .condition{
    padding: 3.9em 0 2.375em;
    .h1{
      width: 105%;
    }
    &__props{
      gap: 1em;
      grid-template-columns: 100%;
    }
  }

  .condition-prop{
    clip-path: unset;
    border-radius: 1em;
    transform: unset !important;
  }

  .condition-form{
    margin-top: 1em;
    clip-path: unset;
    border-radius: 1em;
    grid-template-columns: 100%;
    gap: 2.31em;
    padding: 2.75em .7em 3.38em;
    &__nav{
      justify-content: unset;
    }
    &__success{
      height: 100%;
      justify-content: center;
      top: 0;
      &-icon{
        width: 4em;
      }
      &-title{
        font-size: 2em;
      }
    }
    &__desc{
      font-size: 1em;
    }
    &__arrow{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .compare{
    &__title{
      text-align: center;
    }
  }

  .condition{
    &__props{
      gap: 0;
    }
  }

  .condition-prop{
    clip-path: polygon( 100% 19.452%,100% 19.452%,99.961% 18.026%,99.848% 16.67%,99.667% 15.402%,99.423% 14.238%,99.123% 13.197%,98.773% 12.295%,98.377% 11.552%,97.944% 10.983%,97.477% 10.607%,96.983% 10.442%,3.251% 0.35%,3.251% 0.35%,2.729% 0.418%,2.231% 0.723%,1.766% 1.246%,1.34% 1.967%,0.96% 2.866%,0.633% 3.925%,0.367% 5.123%,0.168% 6.442%,0.043% 7.86%,0% 9.36%,0% 80.548%,0% 80.548%,0.039% 81.974%,0.152% 83.33%,0.333% 84.598%,0.577% 85.762%,0.877% 86.803%,1.227% 87.705%,1.622% 88.449%,2.056% 89.017%,2.523% 89.393%,3.017% 89.558%,96.749% 99.65%,96.749% 99.65%,97.271% 99.582%,97.769% 99.277%,98.234% 98.754%,98.66% 98.033%,99.04% 97.134%,99.367% 96.075%,99.633% 94.877%,99.832% 93.558%,99.957% 92.14%,100% 90.64%,100% 19.452% );
    border-radius: unset;
    &:not(:first-child){
      //margin-top: -.25em;
    }
  }

  .condition-form{
    margin-top: .25em;
    border-radius: unset;
    clip-path: polygon( 100% 3.371%,100% 3.371%,99.961% 3.124%,99.848% 2.889%,99.667% 2.669%,99.423% 2.467%,99.123% 2.287%,98.773% 2.131%,98.377% 2.002%,97.944% 1.903%,97.477% 1.838%,96.983% 1.81%,3.251% 0.061%,3.251% 0.061%,2.729% 0.072%,2.231% 0.125%,1.766% 0.216%,1.34% 0.341%,0.96% 0.497%,0.633% 0.68%,0.367% 0.888%,0.168% 1.116%,0.043% 1.362%,0% 1.622%,0% 96.629%,0% 96.629%,0.039% 96.876%,0.152% 97.111%,0.333% 97.331%,0.577% 97.533%,0.877% 97.713%,1.227% 97.869%,1.622% 97.998%,2.056% 98.097%,2.523% 98.162%,3.017% 98.19%,96.749% 99.939%,96.749% 99.939%,97.271% 99.928%,97.769% 99.875%,98.234% 99.784%,98.66% 99.659%,99.04% 99.503%,99.367% 99.32%,99.633% 99.112%,99.832% 98.884%,99.957% 98.638%,100% 98.378%,100% 3.371% );
    .h1{
      text-align: center;
      padding: 0 1em;
    }
    &__fields{
      grid-template-columns: 100%;
    }
  }
}
