.text{
  font-size: 1.25em;
  line-height: 163%;
  *{
    line-height: inherit;
  }
  ul,
  p{
    @include lastM(0 0 1em);
  }
  b{
    font-weight: 700;
  }
  hr{
    height: 1.25em;
    opacity: 0;
    display: block;
  }
  ul{
    padding-left: .4em;
    li{
      padding-left: .8em;
      position: relative;
      &:before{
        content: 'â€¢';
        position: absolute;
        top: 0;
        left: 0;
        background-image: unset;
        width: unset;
        height: unset;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    font-size: .9375em;
    line-height: 140%;
  }
}


