.blog{
  padding: 10.69em 0 6em;
  margin-bottom: -4em;
  overflow: hidden;
  .m-tab{
    margin-right: 2.125em;
    &:last-child{
      margin-right: 0;
    }
  }
  &__items{

  }
  .news-slider{
    margin-top: 2.94em;
    @media screen and (min-width: 993px){
      .swiper{
        &-container{
          width: 100%;
        }
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(3,calc(100% / 3 - 5.5em / 3));
          gap: 2.75em;
          transform: unset !important;
        }
        &-slide{
          width: 100% !important;
          padding-right: 0;
        }
      }
    }
  }
  &__nav{
    margin-top: 2.875em;
  }
  &__btn{
    padding: 0 2.875em;
  }
}


@media screen and (max-width: 992px){
  .blog {
    padding: 13.62em 0 5.7em;
    margin-bottom: 0;
    &__top{
      display: block;
    }
    .m-tabs{
      display: none;
    }
    .news-slider{
      margin-top: 1em;
    }
    &__nav{
      justify-content: unset;
      margin-top: 1.73em;
    }
  }
}


