@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex-center,
._flex-between,
._flex{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}
.socials{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 .875em 0 0);
    @include center;
    img{
      max-width: 2.1875em;
      max-height: 1.875em;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-10%);
      }
    }
  }
}

.compare-btn,
.favourite-btn{
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
}

.favourite-btn{
  width: 1.4375em;
  &:hover{
    path{
      opacity: 1;
    }
  }
  &._added{
    path{
      opacity: 1;
      stroke: $primary;
      fill: $primary;
    }
  }
}

.compare-btn{
  width: 1.5em;
  &:hover{
    g{
      opacity: 1;
    }
  }
  &._added{
    g{
      opacity: 1;
    }
    line{
      stroke: $primary;
    }
  }
}
