.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        width: 75em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        position: absolute;
        top: 1em;
        right: 1em;
        @include center;
        margin-left: 1em;
        flex-shrink: 0;
        width: 2em;
        img{
            width: 100%;
        }
    }
    .condition-form{
        &--bg{
            position: absolute;
            top: 5%;
            left: 4%;
            background-color: $primary;
            width: 95%;
            height: 100%;
            z-index: -1;
            transform: rotate(-4deg);
        }
    }
}



